<template>
<div class='demand_list'>
    <div class="pwdError" v-show="isshowerror">
        <img src="../../assets/image/密码错误.png" style="position: relative;width: 4.3rem;height: 0.76rem;">
        <div class="pwdError-mes">{{mess}}</div>
    </div>
    <div class="header">
        <div class="back"  @click.stop="back()">
            <img src="../../assets/image/联合42.png" alt="">
        </div>
        <div class="demand_list_head">已提报</div>
        <div class='add_demand' @click.stop='enterAdddemand()'>
            <div class='add_button'>
                <img src="../../assets/image/add.png" alt="">
                <!-- <div class='add_across'></div>
                <div class='add_vertical'></div> -->
            </div>
        </div>
    </div>
    <v-page class="plan-list-page" style="font-size: 0.3rem;">
        <mt-loadmore ref="loadmore" class="loadmore"
                :top-method="loadTop" v-infinite-scroll="loadBottom" infinite-scroll-disabled="moreLoading01" infinite-scroll-distance="40" infinite-scroll-immediate-check="false" :autoFill="false">
            <div class="activity-list" ref="scrollContainer">
                <div class="activity-list-item" v-for="(item, index) in activities" :key="index" >
                    <div class="activity-list-div" @click.stop='enterDemandLook(item.id)' :style="{'border-bottom': index == (activities.length - 1) ? 'none' : GLOBAL.lineStyle}">
                        <div class="activity-list-right">
                            <div class="show-img-one">
                                    <img src="../../assets/image/运检.png" alt="" v-if='item.type == GLOBAL.DEMAND.yj'>
                                    <img src="../../assets/image/通信.png" alt="" v-if='item.type == GLOBAL.DEMAND.tx'>
                                    <img src="../../assets/image/大修.png" alt="" v-if='item.type == GLOBAL.DEMAND.dx'>
                                    <img src="../../assets/image/技改.png" alt="" v-if='item.type == GLOBAL.DEMAND.jg'>
                                    <img src="../../assets/image/人资.png" alt="" v-if='item.type == GLOBAL.DEMAND.rz'>
                                    <img src="../../assets/image/工会.png" alt="" v-if='item.type == GLOBAL.DEMAND.gh'>
                            </div>
                            <div class='show_center'>
                                <div class="activity-list-info">
                                    <div class="activity-list-actName">
                                        <div class="activity-list-allMes">{{ item.requireName }}</div>
                                    </div>
                                </div>
                                <div class="activity-list-word">
                                    <div class='activity-list-type' v-if='item.type == GLOBAL.DEMAND.yj' style='background:rgba(167, 163, 255, 0.09);color:#A49FFF'>运检</div>
                                    <div class='activity-list-type' v-if='item.type == GLOBAL.DEMAND.tx' style='background:rgba(102, 211, 157, 0.1);color:#5794F6'>通信</div>
                                    <div class='activity-list-type' v-if='item.type == GLOBAL.DEMAND.dx' style='background:rgba(255, 241, 235, 1);color:#FFA06B'>大修</div>
                                    <div class='activity-list-type' v-if='item.type == GLOBAL.DEMAND.jg' style='background:rgba(255, 112, 112, 0.1);color:#FF9090'>技改</div>
                                    <div class='activity-list-type' v-if='item.type == GLOBAL.DEMAND.rz' style='background:rgba(255, 158, 158, 0.1);color:#FF9090'>人资</div>
                                    <div class='activity-list-type' v-if='item.type == GLOBAL.DEMAND.gh' style='background:rgba(242, 153, 232, 0.1);color:#EE8BD4'>工会</div>
                                    <div class="activity-list-time" v-if="item.createrTime">
                                        {{item.createrTime}}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="more_loading" style="height:0.30rem; line-height:0.30rem; text-align:center" v-show="!queryLoading01">
                    <div type="snake" color="#00ccff" :size="20" v-show="moreLoading01 && !allLoaded01">加载中...</div>
                    <div v-show="allLoaded01" style="color:#ccc;margin-top:0.2rem;">---- 看到底线了 ----</div>
                </div>
            </div>
        </mt-loadmore>

        <div class="list-empty-show" v-if="isEmpty">
            暂无数据
        </div>
    </v-page>
    <div class='empty'>
        <img src="../../assets/image/空空如也.png" alt="">
        <div class='emptyWord'>空空如也~</div>
    </div>
    <!-- 加载页面 -->
    <Loading :dialogVisible="verDialogVisible"></Loading>
</div>
</template>

<script>
import Loading from './../components/loading'
import API from '@/api/api'
import $ from 'jquery'
import { Indicator } from 'mint-ui'

export default {
  name: 'activity',
  components: {
    Loading
  },
  data () {
    return {
      showMess: 0,
      pageCount: 100,
      pageSize: 10,
      pageNum: 1,
      isEmpty: false, // 列表查询结果是否为空
      activities: [], // 列表内容
      allLoaded: false, // 列表数据是否全部加载
      topStatus: false, // 顶部下拉刷新
      queryLoading01: false,
      moreLoading01: false,
      allLoaded01: false,
      demandtype: [], // 需求类型
      mess: '',
      isshowerror: false, // 是否展示提报成功失败弹窗
      verDialogVisible: true // 加载页
    }
  },
  created () {
    this.getClassifyList()
    this.init('down')
    if (!this.$route.params.show) {
      this.isshowerror = false
    } else {
      if (this.$route.params.show === '0') {
        this.isshowerror = true
        this.mess = '提报失败'
        return
      }
      if (this.$route.params.show === '1') {
        this.isshowerror = true
        this.mess = '提报成功'
      }
    }
  },
  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
    $('.empty').hide()
    window.onresize = function () {
      if ($(document).height() < 400) {
        $('.activity-list').css('min-height', '5.4rem')
      } else {
        $('.activity-list').css('min-height', '11.12rem')
      }
    }
  },
  methods: {
    // 进入提报预览界面
    enterDemandLook (id) {
      this.$router.push({
        name: 'demand_look',
        params: { id: id }
      })
    },
    // 返回键
    onBackKeyDown () {
      this.back()
    },
    // 返回上一路由
    back () {
      this.$router.push({
        name: 'set'
      })
    },
    // 新增需求提报
    enterAdddemand () {
      this.$router.push({
        name: 'demand_add'
      })
    },
    // 下拉刷新
    loadTop () {
      this.pageNum = 1
      this.allLoaded = false
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.init('down')
      this.$refs.loadmore.onTopLoaded()
    },
    // 上拉加载
    loadBottom () {
      if (this.allLoaded01) {
        this.moreLoading01 = true
        return
      }
      if (this.queryLoading01) {
        return
      }
      this.moreLoading01 = !this.queryLoading01
      setTimeout(() => {
        this.pageNum++
        this.init('up')
      }, 500)
    },
    // 需求类型
    getClassifyList () {
      API.post('getClassifyList', {
        dicId: this.GLOBAL.DEMAND.dicId
      }, 'json', (data, err) => {
        if (!data.success) {
          console.log(data.msg)
        } else if (data.success) {
          this.demandtype = data.data
        }
      })
    },
    // 需求提报列表
    init (flag) {
      this.queryLoading01 = false
      this.moreLoading01 = false
      this.allLoaded01 = false
      const that = this
      API.post('findDRPage', {
        pageNum: that.pageNum,
        pageSize: that.pageSize
      }, 'json', (data, err) => {
        if (!data.success) {
          console.log(data.msg)
          that.verDialogVisible = false
        } else if (data.success) {
          if (flag === 'down') {
            this.activities = []
          }
          this.pageCount = data.data.pageCount
          that.forBackData(data.data.pageData)
          that.verDialogVisible = false
          setTimeout(() => {
            this.isshowerror = false
          }, 1000)
        }
      })
    },
    // 循环返回的数据
    forBackData (list) {
      for (var i = 0; i < list.length; i++) {
        // eslint-disable-next-line no-param-reassign
        list[i].createrTime = this.GLOBAL.changeTime(list[i].createrTime)
        this.activities.push(list[i])
      }
      this.isEmpty = false
      this.allLoaded01 = this.activities.length === this.pageCount || list.length === 1
      this.moreLoading01 = this.allLoaded01
      Indicator.close()
      if (this.activities.length === 0) {
        $('.empty').show()
        $('.more_loading').hide()
      } else {
        $('.empty').hide()
        $('.more_loading').show()
      }
    }
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  }

}
</script>
<style scoped>
    .demand_list{
        width:100vw;
        height: 100vh;
    }
    .pwdError{
        height: 0.76rem;
        width: 100%;
        position: absolute;
        top:70%;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .pwdError-mes{
        color: #fff;
        font-size: 0.24rem;
        position: absolute
    }
    .demand_list .header{
        width:100%;
        background:#fff;
        font-size:0.36rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        display: flex;
        justify-content: center;
        z-index: 2;
    }
    .demand_list .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 99;
    }
    .demand_list .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .demand_list_head{
        line-height: 0.94rem;
        font-size:0.38rem;
        color:#4E4E4E;
        width:3rem;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
        text-align: center;
        padding-top:0.02rem;
    }
    .add_demand{
        width:0.7rem;
        height:0.94rem;
        position:fixed;
        top:0;
        right:0;
        display: flex;
        align-items: center;
    }
    .add_button{
        width:0.4rem;
        height:0.4rem;
        position: relative;
    }
    .add_button img{
        width:0.4rem;
        height:0.4rem;
        display: block;
    }
    .add_demand .add_across{
        width:0.4rem;
        height:0.04rem;
        position: absolute;
        background: #5794F6;
        top:0.18rem;
        left:0;
    }
    .add_demand .add_vertical{
        width:0.04rem;
        height:0.4rem;
        position: absolute;
        background: #5794F6;
        top:0;
        left:0.18rem;
    }
    .activity-list{
        width: 100vw;
        height: calc(100vh - 0.94rem);
        padding-top:0.1rem;
    }
    .activity-list-item {
        width: 100%;
        height: 1.8rem;
        /* padding: 0 0.24rem; */
        margin: 0.1rem 0;
    }
    .activity-list-item:first-child{
        margin-top:0;
    }
    .activity-list-div {
        width: 100%;
        height: 1.8rem;
        display: flex;
        justify-content: space-between;
        background:rgba(255,255,255,1);
        border: 0.01rem solid #FFFFFF;
        align-items: center;
        padding: 0 0.3rem;
    }
    .activity-list-right {
        display: flex;
        height: 100%;
        align-items: center;
        width:100%;
    }
    .activity-list-actName {
        /* line-height: 0.32rem; */
        /* height:0.4rem; */
        font-size: 0.36rem;
        color: #4E4E4E;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
    .mint-loadmore-top{
        font-size: 0.3rem;
    }
    .show-img-one{
        /* float: left; */
        width: 1.2rem;
        height: 1.2rem;
        /* background-size: cover;
        background-image: url('../../assets/image/组13.png'); */
        display: flex;
        justify-content: space-between;
    }
    .show-img-one img{
        width: 1.2rem;
        height: 1.2rem;
        display: block;
    }
    .show_center{
        margin-left:0.29rem;
        height:1.2rem;
        /* padding-bottom:0.1rem; */
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        /* width:4.12rem; */
        width:5rem;
    }
    .show-score{
        /* width: 1rem; */
        font-size:0.38rem;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(4,205,157,1);
    }
    .pass{
        color:rgba(4,205,157,1);
        background:#e5faf5;
    }
    .mint-loadmore{
        max-height: calc(100vh - 0.94rem);
        overflow-y: auto;
        margin-top: 0.94rem;
        background: rgba(242,242,242,1);

    }
    .header{
        width:100%;
        background:rgba(4,205,157,1);
        font-size:0.34rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .testHead{
        line-height: 0.94rem;
        font-size:0.34rem;
        color:#ffffff;
    }
    .activity-list-allMes{
        overflow: hidden;
        text-overflow: ellipsis;
        height: 0.6rem;
        width: 100%;
        white-space: nowrap;
        padding-top:0.1rem;
        /*! autoprefixer: off */
         /* autoprefixer: on */
    }
    .activity-list-word{
        display: flex;
        width:100%;
        padding-bottom: 0.2rem;
    }
    .activity-list-word .activity-list-type{
        height:0.32rem;
        /* width:0.82rem; */
        background: #FFF1EB;
        border-radius: 0.06rem;
        font-size:0.24rem;
        color:#FFA06B;
        /* display: flex;
        align-items: center; */
        padding:0 0.08rem;
        /* justify-content: center; */
        line-height: 0.36rem;
    }
    .activity-list-word .activity-list-time{
        display: flex;
        align-items: center;
        margin-left: 0.09rem;
        /* margin-top:0.01rem; */
        color:#9FA7AF;
        font-size:0.26rem;
    }
    .empty{
        width: 100%;
        height: calc(100% - 0.94rem );
        /* margin-top:0.94rem; */
        display:flex;
        padding-top:1.81rem;
        /* justify-content: center; */
        flex-direction: column;
        align-items: center;
        position: absolute;
        z-index:3;
        margin-top: 0.94rem;
        background: #fff;
    }
    .empty img{
        width:2.91rem;
        height:2.63rem;
    }
    .empty .emptyWord{
        margin-top:0.4rem;
        color:#9FA7AF;
        font-size:0.32rem;
    }
</style>



